
































// Imports
import Vue from 'vue';
import AUTH from '@/store/actions/auth';

export default Vue.extend({
  data:() => ({
    username: '' as string,
    password: '' as string,
  }),
  created() {
    const appComponents = {
      appBar: false,
      navigationDrawer: false,
    };
    this.settingAppComponents(appComponents);
  },
  methods: {
    settingAppComponents(appComponents: any) {
      this.$emit('settingAppComponents', appComponents);
    },
    login() {
      const { password, username } = this;
      this.$store.dispatch(AUTH.REQUEST, { password, username })
        .then(() => {
          const appComponents = {
            appBar: true,
            navigationDrawer: true,
          };
          this.settingAppComponents(appComponents);
          this.$router.push('/');
        })
        .catch((error) => {
          // console.log(error);
        });
    },
  },
});
