






import Vue from 'vue';
import ScheduleStepFilter from '@/components/ScheduleAddModule/ScheduleStepFilter.vue';

export default Vue.extend({
  components: {
    ScheduleStepFilter,
  },
  data:() => ({
    menuWidth: 0,
    dataSearch: undefined as any,
    menuType: 'select',
  }),
  watch: {
    $route: {
      handler(route: any): void {
        const queries = route.query;
        const queriesPath = route.fullPath.replace(route.path, '');
        this.getGroupsByFilter(queries, queriesPath);
        this.setMenuType();
      },
      deep: true,
      immediate: true,
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.menuWidth = this.$el.clientWidth;
    });
  },
  methods: {
    setMenuType() {
      this.$emit('setMenuType', this.menuType);
    },
    getGroupsByFilter(queries: any, queriesPath: any) {
      this.$emit('getGroupsByFilter', queries, queriesPath);
    },
    queriesIsNotEmpty(obj: any) {
      return Object.keys(obj).length;
    },
  },
});
