









































































































































import Vue from 'vue';
import FORMS_EDUCATIONS_DICT from '@/dictionaries/formsEducations';
import COURSES_DICT from '@/dictionaries/courses';
import TYPES from '@/dictionaries/types';
import RepositoryFactory from '@/api/RepositoryFactory';

const FacultiesRepository = RepositoryFactory.get('faculties');
const SpecialitiesRepository = RepositoryFactory.get('specialities');
const ScheduleRepository = RepositoryFactory.get('schedule');

export default Vue.extend({
  data:() => ({
    alert: false,
    menu: false,
    date: false,
    description: false,
    fileInput: false,
    dataChange: false,
    dataFilter: {
      speciality__faculty: undefined as any,
      speciality__education_form: undefined as any,
      speciality: undefined as any,
      course: undefined as any,
    },
    attachments: [] as Array<any>,
    filterWidth: 0,
    faculties: [] as Array<any>,
    formEducations: [] as Array<any>,
    specialities: [] as Array<any>,
    courses: [] as Array<any>,
    types: [] as Array<any>,
    selectedFaculty: undefined as any,
    selectedFormEducations: undefined as any,
    selectedSpeciality: undefined as any,
    selectedCourse: undefined as any,
    selectedDates: [] as Array<any>,
    // selectedDates: [
    //   new Date().toISOString().substring(0, 10),
    //   new Date(Date.now() + 7 * 24 * 60 * 60 * 1000).toISOString().substring(0, 10),
    // ],
    selectedType: undefined as any,
    selectedFiles: [] as any,
    selectedDescription: undefined as any,
    isLoadingFaculties: false,
    isLoadingSpecialities: false,
    scheduleAdded: false,
    attachmentsAdded: false,
  }),
  computed: {
    // dateRangeText() {
    //   return this.selectedDates.join(' ~ ');
    // },
    facultiesIsEmpty() {
      return !this.faculties.length;
    },
    formEducationsIsEmpty() {
      return !this.formEducations.length;
    },
    specialitiesIsEmpty() {
      return !this.specialities.length;
    },
    coursesIsEmpty() {
      return !this.courses.length;
    },
    typesIsEmpty() {
      return !this.types.length;
    },
    selectedDatesIsNotEmpty() {
      return this.selectedDates.length;
    },
    selectedFilesIsNotEmpty() {
      return this.selectedFiles.length;
    },
    selectedGroupsIsNotEmpty() {
      return this.$attrs.selectedGroups.length;
    },
    shapeFormed() {
      let formData;
      if (
        this.selectedFaculty !== undefined
        && this.selectedFormEducations !== undefined
        && this.selectedSpeciality !== undefined
        && this.selectedCourse !== undefined
        && this.selectedDatesIsNotEmpty
        && this.selectedType !== undefined
        && this.selectedFilesIsNotEmpty
        && this.selectedGroupsIsNotEmpty
      ) {
        formData = true;
      } else {
        formData = false;
      }

      return formData;
    },
  },
  watch: {
    $route: {
      handler(route: any): void {
        if (this.facultiesIsEmpty) {
          this.getFaculties();
        }

        const queries = route.query;

        if (Object.keys(queries).length) {
          if (queries.speciality__faculty) {
            this.selectedFaculty = Number(queries.speciality__faculty);

            if (this.formEducationsIsEmpty) {
              this.getFormEducations();
            }
          }

          if (queries.speciality__faculty && queries.speciality__education_form) {
            this.selectedFormEducations = Number(queries.speciality__education_form);

            if (this.specialitiesIsEmpty) {
              this.getSpecialities();
            }
          }

          if (
            queries.speciality__faculty
            && queries.speciality__education_form
            && queries.speciality
          ) {
            this.selectedSpeciality = Number(queries.speciality);

            if (this.coursesIsEmpty) {
              this.getCourses();
            }
          }

          if (
            queries.speciality__faculty
            && queries.speciality__education_form
            && queries.speciality
            && queries.course
          ) {
            this.selectedCourse = Number(queries.course);

            if (!this.date) {
              this.date = true;
            }
          }
        }
      },
      immediate: true,
    },
    selectedType: {
      handler(value: any) {
        if (value !== undefined) {
          this.fileInput = true;
        } else {
          this.fileInput = false;
          this.selectedFiles = [];
          this.selectedDescription = '';
        }
      },
    },
    date: {
      handler(value: any) {
        if (value && this.typesIsEmpty) {
          this.getTypes();
          this.description = true;
        } else {
          this.types = [];
          this.description = false;
          this.selectedType = undefined;
        }
      },
      immediate: true,
    },
    dataFilter: {
      handler(valueNew: any, valueOld: any) {
        if (this.dataChange) {
          this.applyFilter();
        }
      },
      deep: true,
    },
    selectedFaculty: {
      handler(newValue: any, oldValue: any) {
        this.dataFilter.speciality__faculty = newValue;

        if (!newValue) {
          this.formEducations = [];
          this.selectedFormEducations = undefined;
        }
      },
      immediate: true,
    },
    selectedFormEducations: {
      handler(newValue: any, oldValue: any) {
        this.dataFilter.speciality__education_form = newValue;

        if (!newValue) {
          this.specialities = [];
          this.selectedSpeciality = undefined;
        }
      },
      immediate: true,
    },
    selectedSpeciality: {
      handler(newValue: any, oldValue: any) {
        this.dataFilter.speciality = newValue;

        if (!newValue) {
          this.courses = [];
          this.selectedCourse = undefined;
        }
      },
      immediate: true,
    },
    selectedCourse: {
      handler(newValue: any, oldValue: any) {
        this.dataFilter.course = newValue;

        if (!newValue) {
          this.date = false;
        }
      },
      immediate: true,
    },
    selectedFiles(values: any) {
      this.attachments = [];
    },
    attachments(values: any) {
      if (values.length && !this.attachmentsAdded) {
        this.addSchedule();
      }
    },
  },
  methods: {
    async getFaculties() {
      this.isLoadingFaculties = true;
      const response = await FacultiesRepository.get();
      this.faculties = response.data.results;
      this.isLoadingFaculties = false;
    },
    async getSpecialities() {
      this.isLoadingSpecialities = true;
      const response = await SpecialitiesRepository.get();
      this.specialities = response.data.results;
      this.isLoadingSpecialities = false;
    },
    getFormEducations() {
      this.formEducations = FORMS_EDUCATIONS_DICT;
    },
    getCourses() {
      this.courses = COURSES_DICT;
    },
    getTypes() {
      this.types = TYPES;
    },
    async addAttachments() {
      this.attachmentsAdded = true;
      this.selectedFiles.forEach(async (file: any) => {
        const formData = new FormData();
        formData.append('file', file);
        const response = await ScheduleRepository.addAttachment(formData);
        this.attachments.push(response.data.id);
      });
      this.attachmentsAdded = false;
    },
    async addSchedule() {
      this.scheduleAdded = true;
      const scheduleData = {
        date_from: this.selectedDates[0],
        date_to: this.selectedDates[1],
        type: this.selectedType,
        description: this.selectedDescription,
        groups: this.$attrs.selectedGroups,
        attachments: this.attachments,
      };
      const response = await ScheduleRepository.addSchedule(scheduleData);
      console.log(response.data);
      this.resetFilter();
      this.scheduleAdded = false;
    },
    changeDataFilter() {
      this.dataChange = true;
    },
    applyFilter() {
      const data = Object.assign({}, this.dataFilter);
      this.$router.replace({ name: 'schedule-add', query: data }).catch((error) => {});
    },
    resetFilter() {
      (this.$refs.form as any).reset();
    },
  },
});
