










import Vue from 'vue';
import { Group } from 'typings/interface/group';
import ScheduleItem from '@/components/ScheduleModule/ScheduleItem.vue';
import Repository from '@/api/Repository';
import RepositoryFactory from '@/api/RepositoryFactory';

const ScheduleRepository = RepositoryFactory.get('schedule');

export default Vue.extend({
  components: {
    ScheduleItem,
  },
  props: {
    groups: {
      type: Array,
      default: [] as Array<Group>,
    },
  },
  data:() => ({
    selectedGroups: [] as Array<any>,
    userAccess: false,
  }),
  watch: {
    groups(value: any) {
      this.selectedGroups = [0];
    },
    $route: {
      handler(value: any) {
        if (this.$store.getters.isAuthenticated) {
          this.userAccess = true;
        }
      },
      immediate: true,
    },
  },
});
