

















































import Vue from 'vue';
import Repository from '@/api/Repository';
import RepositoryFactory from '@/api/RepositoryFactory';

const ScheduleRepository = RepositoryFactory.get('schedule');

export default Vue.extend({
  props: {
    group: {
      type: Object,
      default: {} as any,
    },
    userAccess: {
      type: Boolean,
      default: false,
    },
  },
  data:() => ({
    schedules: [] as Array<any>,
    isScheduleLoading: false,
    request: true,
    active: false,
  }),
  computed: {
    schedulesIsEmpty() {
      return !this.schedules.length;
    },
  },
  watch: {
    active: {
      handler(value: boolean) {
        if (value && this.request) {
          const groupId = this.group.id;
          this.getSchedule(groupId);
        }
      },
    },
    schedules(value: any) {
      this.request = false;
    },
    group(value: any) {
      this.active = false;
      this.request = true;
    },
  },
  methods: {
    async getSchedule(id: number) {
      this.isScheduleLoading = true;
      const response = await ScheduleRepository.get(id);
      this.schedules = response.data.results;
      this.isScheduleLoading = false;
    },
    async deleteSchedule(id: number) {
      await ScheduleRepository.delete(id);
      this.getSchedule(this.group.id);
    },
    routeToInterfaceOfAddSchedule(group: any) {
      const queries = {
        speciality__faculty: group.speciality.faculty.id,
        speciality__education_form: group.speciality.education_form.id,
        speciality: group.speciality.id,
        course: group.course,
      };
      this.$router.push({ name: 'schedule-add', query: queries });
    },
  },
});
