



























































































import Vue from 'vue';
import _ from 'lodash';
import { Group } from 'typings/interface/group';
import Repository from '@/api/Repository';
import RepositoryFactory from '@/api/RepositoryFactory';
import AUTH from './store/actions/auth';

const GroupsRepository = RepositoryFactory.get('groups');

export default Vue.extend({
  name: 'App',
  components: {},
  data: () => ({
    isAuthenticated: false,
    isGroupsLoading: false,
    groups: [] as Array<any>,
    selectedGroups: [] as Array<any>,
    appBar: true,
    navigationDrawer: true,
    menuType: undefined as any,
    queriesPath: undefined as any,
    filterData: {
      speciality__faculty: undefined as any,
      speciality__education_form: undefined as any,
      speciality: undefined as any,
      course: undefined as any,
      name__icontains: undefined as any,
    },
    debouncedGetGroups: undefined as any,
  }),
  watch: {
    queriesPath: {
      handler(value: any) {
        if (
          this.menuType === 'select'
          && this.filterData.speciality__faculty !== undefined
          && this.filterData.speciality__education_form !== undefined
          && this.filterData.speciality !== undefined
          && this.filterData.course !== undefined
        ) {
          this.getGroupsByQueries();
        } else if (
          this.menuType === 'select'
          && this.filterData.course === undefined
        ) {
          this.groups = [];
        } else if (this.menuType !== 'select') {
          this.getGroupsByQueries();
        }
      },
    },
    groups(values: any) {
      if (!values.length) {
        this.selectedGroups = [];
      } else if (this.menuType === 'select') {
        values.forEach((group: any) => {
          this.selectedGroups.push(group.id);
        });
      }
    },
  },
  created() {
    this.autoLogout();
    this.debouncedGetGroups = _.debounce((groupName: any, routerName: string) => {
      this.routeToSelectedGroup(groupName, routerName);
    }, 300);
  },
  methods: {
    async autoLogout() {
      Repository.interceptors.response.use(
        (response: any) => response,
        (error: any) => {
          if (error.config && error.response && error.response.status === 401) {
            this.$store.dispatch(AUTH.LOGOUT, error);
            this.$router.push('/login');
          }
          return Promise.reject(error.response);
        },
      );
    },
    async getGroupsByQueries() {
      this.isGroupsLoading = true;
      const response = await GroupsRepository.getGroupsByQueries(this.queriesPath);
      this.groups = response.data.results;
      this.isGroupsLoading = false;
    },
    async getGroupsByName(name: any) {
      this.isGroupsLoading = true;
      const response = await GroupsRepository.getGroupsByName(name);
      this.groups = response.data.results;
      this.isGroupsLoading = false;
    },
    onGetGroupsByFilter(queries: any, queriesPath: any) {
      this.filterData = Object.assign({}, this.filterData, queries);
      this.queriesPath = queriesPath;
    },
    settingAppComponents(appComponents: any) {
      this.appBar = appComponents.appBar;
      this.navigationDrawer = appComponents.navigationDrawer;
    },
    onSettingAppComponents(appComponents: any) {
      this.settingAppComponents(appComponents);
    },
    routeToSelectedGroup(groupName: any, routerName: string) {
      const data = this.filterData;
      if (!groupName) { groupName = undefined; }
      data.name__icontains = groupName;
      this.$router.replace({ name: routerName, query: data }).catch((error) => {});
    },
    onSetMenuType(type: any) {
      this.menuType = type;
    },
  },
});
