











































































import Vue from 'vue';
import FORMS_EDUCATIONS_DICT from '@/dictionaries/formsEducations';
import COURSES_DICT from '@/dictionaries/courses';
import RepositoryFactory from '@/api/RepositoryFactory';

const FacultiesRepository = RepositoryFactory.get('faculties');
const SpecialitiesRepository = RepositoryFactory.get('specialities');

export default Vue.extend({
  props: {
    menuWidth: {
      type: Number,
      default: 0,
    },
  },
  data:() => ({
    filter: false,
    dataChange: false,
    dataFilter: {
      speciality__faculty: undefined as any,
      speciality__education_form: undefined as any,
      speciality: undefined as any,
      course: undefined as any,
    },
    filterWidth: 0,
    faculties: [] as Array<any>,
    formEducations: FORMS_EDUCATIONS_DICT,
    specialities: [] as Array<any>,
    courses: COURSES_DICT,
    isSelectedFilter: false,
    selectedFaculty: undefined as any,
    selectedFormEducations: undefined as any,
    selectedSpeciality: undefined as any,
    selectedCourse: undefined as any,
    isLoadingFaculties: false,
    isLoadingSpecialities: false,
  }),
  watch: {
    $route: {
      handler(route: any): void {
        this.dataChange = false;
        this.filter = false;

        if (this.facultiesIsEmpty()) {
          this.getFaculties();
        }
        if (this.specialitiesIsEmpty()) {
          this.getSpecialities();
        }

        const queries = route.query;

        const joinedValues = [
          queries.speciality__faculty,
          queries.speciality__education_form,
          queries.speciality,
          queries.course,
        ].join('');
        this.isSelectedFilter = Boolean(joinedValues);

        if (queries.speciality__faculty) {
          this.selectedFaculty = Number(queries.speciality__faculty);
        }
        if (queries.speciality__education_form) {
          this.selectedFormEducations = Number(queries.speciality__education_form);
        }
        if (queries.speciality) {
          this.selectedSpeciality = Number(queries.speciality);
        }
        if (queries.course) {
          this.selectedCourse = Number(queries.course);
        }
      },
      deep: true,
      immediate: true,
    },
    dataFilter: {
      handler(value: any) {
        this.dataChange = true;
      },
      deep: true,
    },
    selectedFaculty: {
      handler(newValue: any, oldValue: any) {
        this.dataFilter.speciality__faculty = newValue;
      },
      immediate: true,
    },
    selectedFormEducations: {
      handler(newValue: any, oldValue: any) {
        this.dataFilter.speciality__education_form = newValue;
      },
      immediate: true,
    },
    selectedSpeciality: {
      handler(newValue: any, oldValue: any) {
        this.dataFilter.speciality = newValue;
      },
      immediate: true,
    },
    selectedCourse: {
      handler(newValue: any, oldValue: any) {
        this.dataFilter.course = newValue;
      },
      immediate: true,
    },
  },
  methods: {
    async getFaculties() {
      this.isLoadingFaculties = true;
      const response = await FacultiesRepository.get();
      this.faculties = response.data.results;
      this.isLoadingFaculties = false;
    },
    async getSpecialities() {
      this.isLoadingSpecialities = true;
      const response = await SpecialitiesRepository.get();
      this.specialities = response.data.results;
      this.isLoadingSpecialities = false;
    },
    applyFilter() {
      if (this.dataChange) {
        const nameIcontains = {
          name__icontains: this.$route.query.name__icontains,
        };
        const data = Object.assign({}, this.dataFilter, nameIcontains);
        this.$router.replace({ name: 'schedule', query: data }).catch((error) => {
          this.dataChange = false;
        });
      }
    },
    facultiesIsEmpty() {
      return !this.faculties.length;
    },
    specialitiesIsEmpty() {
      return !this.specialities.length;
    },
  },
});
