import Repository from '../Repository';

const resource = '/schedule';

export default {
  get(id: number) {
    return Repository.get(`${resource}/?groups=${id}`);
  },
  addSchedule(data: any) {
    return Repository.post(`${resource}/schedule-set/`, data);
  },
  addAttachment(attachment: any) {
    return Repository.post(`${resource}/attachment-set/`, attachment);
  },
  deleteAttachment(id: number) {
    return Repository.post(`${resource}/attachment-set/${id}`);
  },
  delete(id: number) {
    return Repository.delete(`${resource}/schedule-set/${id}`);
  },
};
